#navigation-bar {
	text-align: center;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	background-color: darkred;
	padding: 15px;
	color: white;
	box-shadow: 0px 10px 15px rgb(192, 122.5, 110);
}

#navigation-bar h1 {
	margin-top: 0;
	font-family: 'Grenze Gotisch', cursive;
	font-weight: 800;
	font-size: 50px;
	text-shadow: 0px 0px 5px white;
}

#cart {
	text-align: left;
	position: absolute;
	top: 30px;
	left: 90%;
	display: flex;
	justify-content: flex-end;
	flex-direction: column;
	font-family: 'Baloo Da 2', cursive;
}

#cart button {
	cursor: pointer;
	margin-top: 15px;
	width: 80px;
	font-family: 'Baloo Da 2', cursive;
}

a {
	color: white;
	font-family: 'Baloo Da 2', cursive;
	margin-left: 10px;
	text-decoration: none;
}

.active {
	text-decoration: underline;
}

#home {
	text-align: center;
}

#home h2 {
	font-family: 'Baloo Da 2', cursive;
	font-size: 30px;
}

#home p {
	font-family: 'Baloo Da 2', cursive;
}

.product-card {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 270px;
	height: 550px;
	background-color: white;
	margin-bottom: 20px;
	border-radius: 15px;
	font-family: 'Baloo Da 2', cursive;
}

.product-card img {
	width: 270px;
	max-height: 350px;
	border-top-left-radius: 15px;
	border-top-right-radius: 15px;
}

.product-info {
	padding: 10px;
	font-weight: 500;
}

.product-card-bottom-section {
	padding-left: 10px;
	padding-bottom: 10px;
}

.product-card-bottom-section button {
	cursor: pointer;
	font-family: 'Baloo Da 2', cursive;
}

.product-card-bottom-section input {
	width: 20px;
}

#products-grid {
	margin-top: 100px;
	margin-left: 20px;
	margin-right: 20px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between;
}

body {
	margin: 0px;
	background-color: beige;
}

.App {
	text-align: center;
}

